import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/share";

export abstract class CachcingServiceBase {

  protected cache<T> (
      getter:   () => Observable<T>, setter: ( val: Observable<T> ) => void,
      retreive: () => Observable<T>
  ): Observable<T> {
    
    const cached = getter();

    if (cached !== undefined) {
      
      return cached;
    } else {

      const val = retreive().share();
      setter(val);
      return val;
    }
  }
}



